define('app4all/routes/application', ['exports', 'ember'], function (exports, Ember) {

  'use strict';

  exports['default'] = Ember['default'].Route.extend({
    actions: {
      back: function back() {
        history.back();
      },

      openLink: function openLink(url) {
        window.open(url, '_system');
      }
    }
  });

});